@use "../util" as *;

.result__title {
   background: url("../assets/images/title-back/result-title.webp"), rgba(20, 20, 20, 0.8);
   background-blend-mode: darken;
   background-size: cover;
   background-repeat: no-repeat;
   background-attachment: fixed;
}

.carousel1 {
   background: url("../assets/images/success/topper-bg.png"), var(--some-background);
   background-size: cover;
   padding: 4em 0;
   .carousel__container {
      img {
         border-radius: 20px;
         height: auto;
      }
   }
}

.excellence {
   background: url("../assets/images/title-back/title-back.webp");
   background-size: cover;
   padding: 3em 0;
   .excellence__container {
      max-width: 800px;
      .section__titlee {
         color: #fff;
         font-size: 2rem;
         font-weight: 700;
         text-align: center;
         margin: 0.3em auto;
         @include breakpoint(medium) {
            font-size: 2.5em;
         }
      }
      .section__description {
         color: #eee;
      }
   }
}

.standard-success {
   padding: 3em 0;
   .section__title {
      margin-bottom: 0.8em;
   }
   .successful {
      padding: 1em;
      border-bottom: 5px solid var(--blue);
      border-radius: 10px;
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 1em;
      @include breakpoint(medium) {
         grid-template-columns: 1fr 1fr 1fr;
      }
      @include breakpoint(large) {
         grid-template-columns: 1fr 1fr 1fr 1fr;
      }
   }
   &:nth-child(even) {
      background: white;
      .successful {
         background: var(--some-background);
      }
   }
   &:nth-child(odd) {
      background: var(--some-background);
      .successful {
         background: white;
      }
   }
}

.insights {
   padding: 3em 0;
   background: var(--some-background);
   .section__title {
      margin: 0.8em auto;
      margin-top: 0em;
   }
   .insights__container {
      border: 2px solid var(--blue);
      border-radius: 20px;
      img {
         border-radius: 20px;
         height: auto;
      }
   }
}

.mastermind {
   padding: 3em 0;
   background: var(--gradient);
   .mastermind__container {
      position: relative;
      max-width: 1000px;
      .mastermind-text {
         margin-bottom: 1em;
         .mm-title {
            color: white;
            font-size: 2rem;
            margin-bottom: 1em;
         }
         .mm-text {
            color: #eee;
         }
         @include breakpoint(medium) {
            padding: 4em;
         }
      }
      .mm-carousel {
         background: white;
         padding: 4em;
         position: relative;
         .mm-person {
            text-align: center;
            display: grid;
            grid-auto-flow: row;
            .mm-img {
               border-radius: 1000px;
               margin-bottom: 0.5em;
               @include breakpoint(medium) {
                  // width: 80%;
                  margin: 0.5em auto;
               }
            }
            .mm-name {
               margin-bottom: 0.5em;
               font-weight: 700;
               color: black;
            }
            .mm-ed {
               margin-bottom: 0.5em;
               font-weight: 600;
               color: var(--blue);
            }
            .mm-role {
               font-weight: 500;
            }
         }
      }
      @include breakpoint(medium) {
         display: grid;
         grid-template-columns: 2fr 1fr;
         align-items: stretch;
      }
   }
   @include breakpoint(medium) {
      padding: 0;
   }
}

