:root {
  --font-body: "Poppins", sans-serif;
}

html,
body {
  font-family: "Poppins", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --blue: hsl(207, 100%, 37%);
  --light-blue: hsl(201, 100%, 47%);
  --blue-rgb: #0068bd;
  --light-blue-rgb: #009cf0;
  --red: hsl(14, 87%, 58%);
  --body-text: hsl(0, 0%, 20%);
  --title-text: hsl(219, 100%, 8%);
  --some-background: hsl(236, 65%, 95%);
  --gradient: linear-gradient(to right, var(--blue), var(--light-blue));
  --gradient-reverse: linear-gradient(to right, var(--light-blue), var(--blue));
}

*,
*::before,
*::after {
  box-sizing: border-box;
  transition: all 250ms;
}

body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

ul[role=list],
ol[role=list] {
  list-style: none;
}

html:focus-within {
  scroll-behavior: smooth;
}

html,
body {
  overflow-x: hidden;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.4;
  position: relative;
  color: #414141;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img,
picture {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
h1,
h2,
h3 {
  margin: 0;
  line-height: 1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.section__title {
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  margin: 0.3em auto;
  background: linear-gradient(130deg, #00a6ff, #0085cc, #005499, #0085cc, #00a6ff);
  background-size: 200% auto;
  color: var(--blue);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: shine 5s linear infinite;
}
@keyframes shine {
  to {
    background-position: 200% center;
  }
}
@media (min-width: 40em) {
