.navsec {
  position: relative;
  box-shadow: 0px 10px 10px 0px rgba(0, 0, 0, 0.3);
}
.navsec .navbar {
  max-width: 1200px;
  margin: auto;
  display: grid;
  grid-template-areas: "ham logo ." "link link link";
  align-items: center;
  padding: 0.3em 1em;
}
.navsec .navbar .hamburger {
  grid-area: ham;
  width: 25px;
  height: 3px;
  background: var(--title-text);
  border-radius: 100px;
  transition: all 250ms;
}
.navsec .navbar .hamburger::after, .navsec .navbar .hamburger::before {
  display: block;
  content: "";
  background: var(--title-text);
  height: 3px;
  border-radius: 100px;
  width: 115%;
  transition: all 250ms;
}
.navsec .navbar .hamburger::after {
  transform: translateY(-11px);
}
.navsec .navbar .hamburger::before {
  transform: translateY(8px);
}
.navsec .navbar .nav-logo {
  grid-area: logo;
}
.navsec .navbar .nav-logo img {
  width: 40%;
  margin: auto;
}
.navsec .navbar .nav-links {
  display: block;
  grid-area: link;
  list-style-type: none;
  text-align: center;
  padding-left: 0;
  max-height: 0;
  overflow: hidden;
  transition: all 250ms;
  margin-bottom: -0.5em;
  position: relative;
}
.navsec .navbar .nav-links a {
  color: var(--blue);
}
@media (min-width: 40em) {
  .navsec .navbar .nav-links {
    overflow: visible;
  }
}
.navsec .navbar .nav-links .nav__link {
  color: var(--blue);
  font-weight: 600;
  padding: 1em 0;
  display: block;
  transition: all 250ms;
  position: relative;
}
@media (min-width: 40em) {
  .navsec .navbar .nav-links .nav__link {
    overflow: visible;
  }
}
.navsec .navbar .nav-links .nav__link::after, .navsec .navbar .nav-links .nav__link::before {
  position: absolute;
  content: "";
  width: 0%;
  height: 3px;
  background: var(--gradient);
  transition: width 300ms;
}
.navsec .navbar .nav-links .nav__link::after {
  right: 10%;
  bottom: 0;
}
.navsec .navbar .nav-links .nav__link::before {
  left: 10%;
  top: 0;
}
.navsec .navbar .nav-links .nav__link:hover::after, .navsec .navbar .nav-links .nav__link:hover::before {
  width: 80%;
  transition: width 300ms;
}
.navsec .navbar .nav-links .sub-list-parent {
  color: var(--blue);
}
.navsec .navbar .nav-links .sub-list-parent .down-arrow {
  position: absolute;
  margin-left: 0.3rem;
  color: var(--blue);
  transform: rotate(90deg);
}
@media (min-width: 40em) {
  .navsec .navbar .nav-links .sub-list-parent:hover .sub-list {
    max-height: 1000px;
  }
  .navsec .navbar .nav-links .sub-list-parent:hover .down-arrow {
    transform: rotate(0deg);
  }
}
.navsec .navbar .nav-links .sub-list-parent .sub-list {
  padding-left: 0;
  list-style-type: none;
  overflow: auto;
  z-index: 111 !important;
  background: white;
  left: 0;
  width: auto;
  transition: max-height 0ms;
  max-height: 0;
}
@media (min-width: 40em) {
  .navsec .navbar .nav-links .sub-list-parent .sub-list {
    position: absolute;
  }
  .navsec .navbar .nav-links .sub-list-parent .sub-list:hover {
    max-height: 1000px;
  }
}
.navsec .navbar .nav-links .sub-list-parent .sub-list .sub-list-item {
  margin: 0.3rem 1rem;
}
.navsec .navbar .nav-links .sub-list-parent .sub-list .sub-list-item:nth-child(1) {
  margin-top: 5rem;
}
.navsec .navbar .nav-links .sub-list-parent .sub-list .sub-list-item a {
  color: var(--blue);
}
@media (min-width: 40em) {
  .navsec .navbar .nav-links .sub-list-parent .sub-list .sub-list-item {
    margin-bottom: 1rem;
  }
}
.navsec .navbar .nav-links .sub-list-parent .max-height {
  max-height: 1000px;
}
@media (min-width: 40em) {
  .navsec .navbar {
    grid-template-columns: 15% 85%;
    grid-template-areas: "logo link";
  }
  .navsec .navbar .hamburger {
    display: none;
  }
  .navsec .navbar .nav-logo {
    text-align: center;
  }
  .navsec .navbar .nav-logo img {
    width: 100%;
    margin: auto;
  }
  .navsec .navbar .nav-links {
    max-height: 1000px;
    padding: 0.5em 0;
    margin: 0.7em auto;
    margin-right: 1em;
  }
  .navsec .navbar .nav-links .nav__link {
    display: inline-block;
    padding: 0.5em 1em;
  }
}

